<template>
  <div id="app" v-if='webReady'>
    <router-view name='Header'></router-view>
    <!--banner start-->
		<div class="banner">
			<div class="banner-inner">
				<div class="swiper-container1">
				    <div class="swiper-wrapper">
				        <div class="swiper-slide" v-for="(item,index) in indexData.Swiper" :key="index" :style="{background: 'url('+item.url+')no-repeat center',backgroundSize: 'cover'}">
				        	<div class="mask">
				        		<div class="text-box-1">
				        			<h3>{{item.title}}</h3>
				        			<p class="ms">{{item.smt}}</p>
				        		</div>
				        	</div>
				        </div>
				    </div>
				    <!-- 如果需要分页器 -->
				    <div class="swiper-pagination1"></div>
				</div>
			</div>
		</div>
		<!--banner end-->
		<!--major start-->
		<div class="five-major">
			<div class="five-major-inner clear-spacing">
				<div :class="['item',item.ms?'item2':'']" v-for="(item,index) in indexData.Technology" :key="index">
					<a :href="'/technology.html?tnlgid='+item.tnlgid">
						<i class="icon"></i>
						<div class="text">
							<p>{{item.title}}</p>
							<p v-if="item.ms" class="ms">{{item.ms}}</p>
						</div>
					</a>
				</div>
			</div>
		</div>
		<!--major end-->
		<!--content start-->
		<div class="content-outer">
			<canvas id="J_dotLine" width="1920" height="4000"></canvas>
			<div class="content-inner">
				<div class="index-content clearfix">
					<div class="item">
						<div class="item-inner">
							<div class="blink-title">
								文章
							</div>
							<div class="article-list-outer">
								<div class="first-item">
									<a :href="'/newsDetails.html?newsid='+indexData.News.firstNew.newsid">
										<h3 class="text-of"><i class="icon">1</i>Syscoin 社区每周更新 #6</h3>
										<div class="text-box clearfix">
											<div class="text">
												{{indexData.News.firstNew.text}}
											</div>
											<div class="img-box">
												<img :src='indexData.News.firstNew.url' alt="" />
												<p class="time">{{indexData.News.firstNew.time}}</p>
											</div>
										</div>	
									</a>								
								</div>
								<ul class="article-list">
									<li v-for="(item,index) in indexData.News.newsList" :key="index">
										<a :href="'/newsDetails.html?newsid='+item.newsid">
											<div class="list-box fbox">
												<p class="text-of">
													<i class="icon">2</i>
													{{item.title}}
												</p>
												<span class="time">
													{{item.time}}
												</span>
											</div>
										</a>
									</li>
								</ul>								
							</div>
							<div class="blink-more">
								<a href="/newsCenter.html">更多文章>></a>
							</div>
						</div>
					</div>
					<div class="item">
						<div class="item-inner">
							<div class="blink-title">
								学院 
							</div>
							<div class="college-list">
								<ul>
									<li v-for='(item,index) in indexData.College' :key="index">
										<a :href="'/college.html?colid='+item.colid">
											<h3 class="text-of">{{item.title}}</h3>
											<p class="ms text-of">{{item.smt}}</p>
										</a>
									</li>
								</ul>
							</div>	
							<div class="blink-more">
								<a href="/collegeCenter.html">更多学院>></a>
							</div>
						</div>
					</div>
					<div class="item" v-for="(item,index) in indexData.Video" :key="index">
						<div class="item-inner">
							<div class="event-list">
								<div class="event-item">
									<div class="img-box">
										<img :src="item.turepic" alt="" />
										<div class="video-mask">
											<div class="icon" :data-src='item.url' @click="playVideo(item.url)"></div>
										</div>
									</div>
									<div class="text-box">
										<h3 class="text-of">{{item.title}}</h3>	
										<p class="video-text">{{item.smt}}</p>								
									</div>
								</div>								
							</div>
						</div>
					</div>
					<div class="item">
						<div class="item-inner">
							<div class="blink-title">
								合作项目 
							</div>
							<div class="project-list">
								<div class="project-item" v-for="(item,index) in indexData.Project" :key="index">
									<a :href="'/poject.html?projid='+item.projid">
										<img :src="item.url" alt="" />
										<div class="mask">
											<p class="tt">{{item.title}}</p>
											<p class="text">
												{{item.smt}}
											</p>
											<p class="project-more">
												<a href="/project.html">阅读更多</a>
											</p>
										</div>
									</a>									
								</div>
							</div>	
							<div class="blink-more">
								<a href="/project.html">更多项目>></a>
							</div>
						</div>
					</div>
					<div class="item">
						<div class="item-inner">
							<div class="blink-title">
								活动 
							</div>
							<div class="event-list">
								<div class="event-item" v-for="(item,index) in indexData.EventList" :key="index">
									<div class="img-box">
										<img :src="item.img" alt="" />
									</div>
									<div class="text-box">
										<h3 class="text-of">{{item.title}}</h3>
										<p class="address">
											{{item.address}}
										</p>
										<p class="time">
											{{item.time}}
										</p>
										<div class="event-more">
											<a href="javascript:;" class="togo" @click="showEvent(index)">GO >></a>
										</div>										
									</div>
								</div>
							</div>
							<div class="blink-more">
								<a href="">更多活动>></a>
							</div>
						</div>
					</div>
				</div>
			</div>
			<!--event mask-->
			<div class="event-mask" v-if="EventBol">
				<div class="event-mask-outer">
					<div class="event-mask-inner" :style="{'background': 'url('+indexData.EventList[EventIndex].truepic+')no-repeat center'}">
						<div class="mask-item"></div>
						<div class="mask-item">
							<h3 class="text-of">{{indexData.EventList[EventIndex].truepic}}</h3>
							<div class="pic-box clearfix">
								<img src="/static/images/asd455.jpg" alt="" />
								<p class="name text-of">{{indexData.EventList[EventIndex].name}}</p>
								<p class="text-of">{{indexData.EventList[EventIndex].ms}}</p>
							</div>
							<div class="address">{{indexData.EventList[EventIndex].address}}</div>
							<div class="time">
								{{indexData.EventList[EventIndex].time}}
							</div>
							<div class="colse" @click="colseEvent">关闭</div>
						</div>
					</div>
				</div>
			</div>
			<!-- video mask -->
			<div class="video-outer" :style="{display:vedioBol?'block':'none'}">
				<div class="video-inner">
					<video src="" id="vid" controls="">
						你的浏览器暂不支持H5 video!
					</video>
					<div class="colse-video" @click="colseVideo">×</div>
				</div>
			</div>
		</div>
		<!--content end-->
    <router-view name='Footer'></router-view>
  </div>
</template>

<script>
export default {
	name: 'index',
  data(){
    return {
			webReady:false,
			navBarIndex:0,
			indexData:'',
			vedioBol:false,
			EventBol:false,
			EventIndex:0,

    }
	},
	methods: {
		showEvent(index){
			this.EventIndex = index
			this.EventBol = true
		},
		colseEvent(){
			this.EventBol = false
		},
		playVideo(url){
			$('#vid').attr('src',url);
			this.vedioBol = true
			$('#vid').trigger('play');
		},
		colseVideo(){
			this.vedioBol = false
			$('#vid').trigger('pause');
		},
		getData(){
			let that = this
			$.ajax({
				type:'POST',
				url:'/api/index.php',
				data:{},
				success:function(res){				
					let Json = eval('('+res+')');
					console.log(Json)
					that.indexData = Json
					that.webReady = true				
				},
				error:function(res){
					console.log(res)
				}
			})
		}
	},
	created() {
		this.getData();
	},
	watch: {//监听
		webReady:function(){//webReady发生变化
			this.$nextTick(function(){//webReady发生变化dom渲染完后
				lizishanguang(document.getElementById('J_dotLine'))
				var mySwiper1 = new Swiper ('.swiper-container1', {
					speed:500,
					loop:true,
					autoplay:4000,
					pagination: '.swiper-pagination1',
					autoplayDisableOnInteraction:false
				}) 
			})	
		}
	},
  mounted() {	
		var that = this		
		$(document).keydown(function(event) {
			if(event.keyCode==27){
				that.vedioBol = false
				$('#vid').trigger('pause');
			}
		})		
	}
}
</script>




